<template>
    <div class="row justify-content-center">
        <div>
            <p>[TESTING ONLY] Select Active User</p>
            <select id="users" @change="getActiveUserId($event)" v-model="activeUser">
                <label for="user">Choose a user:</label>
                <option v-for="user in Users" v-bind:key="user.key" v-bind:value="user.key">
                      {{ user.firstName }}
                </option>
            </select>
        </div>
        <br><br>
        <div class="col-md-5">
            <form @submit.prevent="submitForm">
                <div class="form-group">
                    <label for="location">Name:</label>
                    <input type="text" class="form-control" id="name" v-model="trip.name">
                </div>
                <div class="form-group">
                    <label for="location">Location:</label>
                    <input type="text" class="form-control" id="location" v-model="trip.location">
                </div>
                <div class="form-group">
                    <label for="start-date">Start Date:</label>
                    <input type="date" class="form-control" id="start-date" v-model="trip.startDate">
                </div>
                <div class="form-group">
                    <label for="end-date">End Date:</label>
                    <input type="date" class="form-control" id="end-date" v-model="trip.endDate">
                </div>
                <button class="btn btn-outline-dark" type="submit">Submit</button>
            </form>
        </div>
    </div>
</template>

<script>
    import { db } from '../firebaseDb';
    export default {
        data() {
            return {
                activeUser: null,
                Users: [],
                trip: {}
            };
        },
        created() {
            db.collection('users').get().then(querySnapshot => {
                querySnapshot.forEach(item => {
                    //console.log(item.data())
                    this.Users.push({
                        key: item.id,
                        firstName: item.data().firstName,
                        lastName: item.data().lastName,
                        email: item.data().email
                    })
                })
            })
        },
        watch: {

        },
        methods: {
            getActiveUserId(event) {
                console.log(event.target.value)

            },
            submitForm(event) {
                this.trip.userId = this.activeUser
                console.log(this.trip)
                event.preventDefault()
                db.collection('trips').add(this.trip).then(() => {
                    alert("Trip successfully created!");
                    this.trip.userId = this.activeUser
                    this.trip.name = ''
                    // this.trip.startDate = ''
                    // this.trip.endDate = ''
                    this.trip.userId = ''
                }).catch((error) => {
                    console.log(error);
                });
            }
        }
    };


</script>
