<template>
    <div class="row">
        <div>
            <p>[TESTING ONLY] Select Active User</p>
            <select id="users" @change="getActiveUserId($event)" v-model="activeUser">
                <label for="user">Choose a user:</label>
                <option v-for="user in Users" v-bind:key="user.key" v-bind:value="user.key">
                      {{ user.firstName }}
                </option>
            </select>
        </div>
        <div>
            <h1>Friend Requests</h1>
            <div class="col-md-12" v-if="this.hasOpenFriendRequests">
                <table class="table table-striped">
                    <thead>
                        <tr>
                            <th>Name</th>
                            <th>email</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="friendRequest in openFriendRequests" :key="friendRequest.key">
                            <td>{{ friendRequest.firstName }} {{friendRequest.lastName}}</td>
                            <td>{{ friendRequest.email }}</td>
                            <td>
                                <button @click.prevent="acceptFriendRequest(friendRequest.key,friendRequest.recipientId,friendRequest.senderId)" class="btn btn-outline-dark">Accept</button>
                                <button @click.prevent="deleteFriendRequest(friendRequest.key,friendRequest.recipientId)" class="btn btn-outline-dark">Remove</button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <div v-else>
                <h4>No open friend requests</h4>
            </div>
        </div>
        <br>
        <div>
            <h1>Find Friends</h1>
            <form @submit.prevent="submitForm">
                <div>
                    <label for="email">Email:</label>
                    <input type="text" id="email" v-model="email">
                </div>
                <button type="submit">Find Friend</button>
            </form>
            <div class="row">
                <div class="col-md-12">
                    <table class="table table-striped">
                        <thead>
                            <tr>
                                <th>Name</th>
                                <th>Email</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="user in queriedUser" :key="user.key">
                                <td>{{ user.firstName }} {{user.lastName}}</td>
                                <td>{{ user.email }}</td>
                                <td>
                                    <button @click.prevent="sendFriendRequest(user.key)" class="btn btn-outline-dark">Add Friend</button>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
        <div>
            <br><br>
            <h1>My Friends</h1>
            <p>list of my friends</p>
            <div class="row">
                <div class="col-md-12">
                    <table class="table table-striped">
                        <thead>
                            <tr>
                                <th>Name</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="friend in friends" :key="friend.key">
                                <td>{{ friend.firstName }} {{friend.lastName}}</td>
                                <td>{{ friend.email }}</td>
                                <td>
                                    <button @click.prevent="removeFriend(friend.key, friend.activeUserId)" class="btn btn-outline-dark">Remove Friend</button>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import { db } from '../firebaseDb';

    export default {
        components() {},
        data() {
            return {
                Users: [],
                activeUser: null,
                email: null,
                queriedUser: [],
                friendRequest: {},
                storeOpenFriendRequestIds: [],
                openFriendRequests: [],
                newFriendEntry: {},
                friends: [],
                hasOpenFriendRequests: null
            }
        },
        created() {
            db.collection('users').get().then(querySnapshot => {
                querySnapshot.forEach(item => {
                    this.Users.push({
                        key: item.id,
                        firstName: item.data().firstName,
                        lastName: item.data().lastName,
                        email: item.data().email
                    })
                })
            })
        },
        methods: {
            getActiveUserId(event) {
                this.listOpenFriendRequests(event.target.value)
                this.getFriends(event.target.value)
            },
            submitForm() {
                event.preventDefault()
                this.queriedUser = [] // reset each variable
                db.collection('users').where("email", "==", this.email).get().then(querySnapshot => {
                    querySnapshot.forEach(item => {
                        this.queriedUser.push({
                            key: item.id,
                            firstName: item.data().firstName,
                            lastName: item.data().lastName,
                            email: item.data().email
                        })
                    })
                })
            },
            sendFriendRequest(userId) {
                this.friendRequest.senderId = this.activeUser
                this.friendRequest.recipientId = userId

                // submit value in friendRequest table
                db.collection('friendRequests').add(this.friendRequest).then(() => {
                    alert("Friend Request Sent!");
                }).catch((error) => {
                    console.log(error);
                });

            },
            listOpenFriendRequests(activeUserId) {
                // pull friend requests for user
                this.storeOpenFriendRequestIds = [] // reset each variable
                this.hasOpenFriendRequests = false
                this.openFriendRequests = []
                db.collection('friendRequests').where("recipientId", "==", activeUserId).get().then(querySnapshot => {
                    querySnapshot.forEach(item => {
                        this.hasOpenFriendRequests = true; // T/F indicator that a friend request exists for user for rendering
                        //save the friend request key and senderId
                        this.storeOpenFriendRequestIds.push({
                            key: item.id,
                            senderId: item.data().senderId,
                        })
                        // query user table to get user name & email
                        db.collection("users").doc(item.data().senderId).get().then(snapshot => {
                            //this.openFriendRequests = snapshot.data().firstName
                            this.openFriendRequests.push({
                                key:item.id,
                                senderId: item.data().senderId,
                                firstName: snapshot.data().firstName,
                                lastName: snapshot.data().lastName,
                                email: snapshot.data().email,
                                recipientId: item.data().recipientId
                            })
                        })
                        console.log("friends ",this.openFriendRequests)
                    })
                })
            },
            deleteFriendRequest(friendRequestKey,recipientId) {
                db.collection("friendRequests").doc(friendRequestKey).delete().then(
                )
                this.listOpenFriendRequests(recipientId) // re-pull open friend requests after friend request is removed
            },
            acceptFriendRequest(friendRequestKey,recipientId,senderId){

                // add friend data to the friends collection
                this.newFriendEntry.userId1 = recipientId
                this.newFriendEntry.userId2 = senderId
                console.log(this.newFriendEntry)
                db.collection('friends').add(this.newFriendEntry).then(() => {
                    alert("Friend Added");

                }).catch((error) => {
                    console.log(error);
                });

                //remove friend request from collection
                db.collection("friendRequests").doc(friendRequestKey).delete().then()
                this.listOpenFriendRequests(recipientId) // re-pull open friend requests after friend request is removed

            },
            getFriends(activeUser) {

                // get user IDs of friends
                this.friends = []
                db.collection('friends').get().then(querySnapshot => {
                    querySnapshot.forEach(item => {
                        if(item.data().userId1 == activeUser || item.data().userId2 == activeUser) {
                            if (item.data().userId1 == activeUser) {

                                db.collection("users").doc(item.data().userId2).get().then(snapshot => {
                                    this.friends.push({
                                        key:item.id, // the key to the friends collection to remove friend
                                        firstName: snapshot.data().firstName,
                                        lastName: snapshot.data().lastName,
                                        activeUserId: activeUser
                                    })
                                })


                            } else {
                                db.collection("users").doc(item.data().userId1).get().then(snapshot => {
                                    this.friends.push({
                                        key:item.id, // the key to the friends collection to remove friend
                                        firstName: snapshot.data().firstName,
                                        lastName: snapshot.data().lastName,
                                        activeUserId: activeUser
                                    })
                                })
                            }
                        }
                    })
                })
                console.log("get friend list")
                console.log(activeUser)
                console.log(this.friends)
            },
            removeFriend(friendRequestKey, activeUser){
                db.collection("friends").doc(friendRequestKey).delete().then(
                    this.getFriends(activeUser) // re-pull open friend requests after friend request is removed
                )
                alert("Friend Removed")
            }

        }
    }
</script>
<style>
    .btn-primary {
        margin-right: 12px;
    }
</style>
