<template>
    <div class="row justify-content-center">
        <div class="col-md-5">
            <p>This page is for testing only - will be replaced by login/sign-up page</p>
            <h3 class="text-center">Add User</h3>
            <form @submit.prevent="onFormSubmit">
                <div class="form-group">
                    <label>First Name</label>
                    <input type="text" class="form-control" v-model="user.firstName" required>
                </div>
                <div class="form-group">
                    <label>Last Name</label>
                    <input type="text" class="form-control" v-model="user.lastName" required>
                </div>
                <div class="form-group">
                    <label>Email</label>
                    <input type="email" class="form-control" v-model="user.email" required>
                </div>
                <div class="form-group">
                    <label>Password</label>
                    <input type="text" class="form-control" v-model="user.password" required>
                </div>
                <div class="form-group">
                    <button class="btn btn-primary btn-block">Add User</button>
                </div>
            </form>
        </div>
    </div>
</template>
<script>
    import { db } from '../firebaseDb';
    export default {
        data() {
            return {
                user: {
                }
            }
        },
        methods: {
            onFormSubmit(event) {
                console.log(this.user)
                event.preventDefault()
                db.collection('users').add(this.user).then(() => {
                    alert("User successfully created!");
                    this.user.firstName = ''
                    this.user.lastName = ''
                    this.user.email = ''
                    this.user.password = ''
                }).catch((error) => {
                    console.log(error);
                });
            }
        }
    }
</script>
